import AbstractView from '../abstract-view';

export default class RulesView extends AbstractView {
  constructor() {
    super();
    this.title = ``;
    this.description = ` 10      <img
      src="img/photo_icon.png" width="16" height="16">   <img
      src="img/paint_icon.png" width="16" height="16" alt="">.<br>
            .<br>
          30 .<br>
          3 .<br>
      <br>
      ?`;
  }

  get template() {
    return `
    <div class="rules">
      <h1 class="rules__title">${this.title}</h1>
      <p class="rules__description">${this.description}</p>
      <form class="rules__form">
        <input class="rules__input" type="text" placeholder=" ">
        <button class="rules__button  continue" type="submit" disabled>Go!</button>
      </form>
    </div>`;
  }

  bind() {
    this.nameField = this.element.querySelector(`.rules__input`);
    this.buttonSubmit = this.element.querySelector(`.rules__button`);

    this.nameField.addEventListener(`input`, () => this.onInputChange(this.nameField.value));

    this.buttonSubmit.onclick = (evt) => {
      evt.preventDefault();
      this.onFormSubmit(this.nameField.value);
    };
  }

  enableButton() {
    this.buttonSubmit.disabled = false;
  }

  disableButton() {
    this.buttonSubmit.disabled = true;
  }

  onInputChange() {}

  onFormSubmit() {}
}
